@-webkit-keyframes blur {
    from {
      text-shadow:0px 0px 10px #fff,
        0px 0px 10px #fff, 
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px -10px 100px #7B96B8; 
    }
  }
  
@keyframes blur {
    from {
      text-shadow:0px 0px 10px #fff,
        0px 0px 10px #fff, 
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px 0px 25px #fff,
        0px -10px 100px #7B96B8; 
    }
  }
  
@-webkit-keyframes fadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}  
@keyframes fadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@media only screen and (max-width: 600px) {
    .intro .container .icons {
        font-size: 2em;
    }
    
    .intro .title {
        font-size: 3em;
    }
    
    .intro .subtitle{
        font-size: 1.1em;
    }
} 


.intro .tooltip {
    position: relative;
    display: inline-block;
    cursor: pointer;
  }

.intro .tooltip .tooltiptext {
    visibility: hidden;
    background-color: hsl(0, 0%, 7%);
    width: 150px;
    border-radius: 6px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -60px;
    font-size: 0.7em;
  }

.intro .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: hsl(0, 0%, 7%) transparent transparent;
  }
  
.intro  .tooltip:hover .tooltiptext {
    visibility: visible;
  }


.intro .downarrow{
    position: absolute;
    align-content: center;
    padding: 1em;
    margin-top: 0.6em;
    margin-bottom: 0.2em;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.downarrow span{
    display: block;
    width: 15px;
    height: 15px;
    border-bottom: 5px solid hsl(217, 71%, 53%);
    border-right: 5px solid hsl(217, 71%, 53%);
    margin: -10px;
    -webkit-animation: animate 2s infinite;
            animation: animate 2s infinite;
}
.downarrow span:nth-child(2){
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
}
.downarrow span:nth-child(3){
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
}
@-webkit-keyframes animate {
    0%{
        opacity: 0;
        -webkit-transform: rotate(45deg) translate(-20px,-20px);
                transform: rotate(45deg) translate(-20px,-20px);
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        -webkit-transform: rotate(45deg) translate(20px,20px);
                transform: rotate(45deg) translate(20px,20px);
    }
}
@keyframes animate {
    0%{
        opacity: 0;
        -webkit-transform: rotate(45deg) translate(-20px,-20px);
                transform: rotate(45deg) translate(-20px,-20px);
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        -webkit-transform: rotate(45deg) translate(20px,20px);
                transform: rotate(45deg) translate(20px,20px);
    }
}
