.social-media:hover .stickyicons{
        -webkit-filter:blur(3px);
                filter:blur(3px);
        opacity:.5;
        -webkit-transform: scale(.98);
                transform: scale(.98);
        box-shadow:none;
}

.hover-content{
    display: none;
}

.social-media:hover .stickyicons:hover{
    -webkit-filter:blur(0px);
            filter:blur(0px);
    opacity:1;
    box-shadow:0 8px 20px 0px rgba(0,0,0,0.125);
    -webkit-transition: all ease-in;
    transition: all ease-in;
    -webkit-transform: translateY(3px) translateX(16px) translatez(20px) scale(1.3);
            transform: translateY(3px) translateX(16px) translatez(20px) scale(1.3);
    -webkit-animation: linear;
            animation: linear;
}

.social-media:hover .stickyicons:hover .hover-content{
    display: inline-block;
    font-size: 20px;
    border-radius: 10px; 
    text-align: center;
    margin: 10px;     
}

